	// function to send email input value to model 
	submit_user_email = (controllerFunction)=>{

		// cache input and btn
		const index_main_section1_div3_span1_btn1 = 
		document.getElementById("index-main-section1-div3-span1-btn1")

		const index_main_section1_div3_span1_input1 =
		document.getElementById("index-main-section1-div3-span1-input1");

		// event listener for btn
		index_main_section1_div3_span1_btn1.addEventListener("click", ()=>{

			// if successful email enter
			if(
				index_main_section1_div3_span1_input1.value.length > 0 &&
				index_main_section1_div3_span1_input1.value.includes("@") &&
				index_main_section1_div3_span1_input1.value.includes(".") &&
				index_main_section1_div3_span1_input1.value.length > 5
			){
				controllerFunction(index_main_section1_div3_span1_input1.value.trim());
			}
			else{
				const index_main_section1_div3_span1_input1 = 
				document.getElementById("index-main-section1-div3-span1-input1");

				const index_main_section1_div3_span2 = 
				document.getElementById("index-main-section1-div3-span2");

				const index_main_section1_div3_span2_h5 = 
				document.getElementById("index-main-section1-div3-span2-h5");

				const loading = 
				document.getElementById("index-main-section1-div3-span2-div1");

				index_main_section1_div3_span2.style.display = "flex";

				index_main_section1_div3_span2_h5.style.display = 'flex';

				loading.style.display = 'none';

				index_main_section1_div3_span2_h5.classList
				.remove("index-main-section1-div3-span2-h5-submitted");
				
				index_main_section1_div3_span2_h5.classList
				.add("index-main-section1-div3-span2-h5-error");

				index_main_section1_div3_span2_h5.textContent 
				= `Error Submitting Email ${index_main_section1_div3_span1_input1.value}`

				index_main_section1_div3_span1_input1.value ='';
			}
		})

		// MOBILE

		const mobile_btn = document.getElementById("mobile-newsletter-form-btn")
		const mobile_input = document.getElementById("mobile-newsletter-form-input")

		mobile_btn.addEventListener("click", ()=>{

			// if successful email enter
			if(
				mobile_input.value.length > 0 &&
				mobile_input.value.includes("@") &&
				mobile_input.value.includes(".") &&
				mobile_input.value.length > 5
			){
				controllerFunction(mobile_input.value.trim());
			}
			else{
				const mobile_div = document.getElementById("mobile-newsletter-status");
				const loading_mobile = document.getElementById("mobile-newsletter-status-loading");
				const success = document.getElementById("mobile-email-success");
				const err = document.getElementById("mobile-email-err");

				mobile_div.style.display = "none";
				mobile_div.style.display = "flex";
				loading_mobile.style.display = "none";
				success.style.display = "none";
				err.style.display = "flex";
				mobile_input.value = "";
			}
		})
	}

	// function for resident in
	render_images = (images) => {
	  // ---------- DESKTOP TARGETS ----------
	  const desktopImg1 = document.getElementById("index-section2-div2-section1-div-img1");
	  const desktopImg2 = document.getElementById("index-section2-div2-section1-div-img2");

	  // ---------- MOBILE TARGETS ----------
	  const mobileImg1 = document.getElementById("index-section2-mobile-div2-section1-div-img1");
	  const mobileImg2 = document.getElementById("index-section2-mobile-div2-section1-div-img2");

	  // ---------- SPLIT IMAGE ARRAYS ----------
	  const array1 = images.filter(
	    (img) =>
	      img.image_dev_url !==
	        "http://localhost:3001/images/resident-involvment-images/resident-involvment8.webp" &&
	      img.image_dev_url !==
	        "http://localhost:3001/images/resident-involvment-images/resident-involvment9.webp"
	  );

	  const array2 = images.filter(
	    (img) =>
	      img.image_dev_url ===
	        "http://localhost:3001/images/resident-involvment-images/resident-involvment8.webp" ||
	      img.image_dev_url ===
	        "http://localhost:3001/images/resident-involvment-images/resident-involvment9.webp"
	  );

	  const getSrc = (img) =>
	    process.env.NODE_ENV === "production"
	      ? img.image_prod_url
	      : img.image_dev_url;

	  // ---------- FADE TRANSITION ----------
	  const updateImage = (imgElement, newSrc) => {
	    imgElement.classList.add("hide");
	    setTimeout(() => {
	      imgElement.src = newSrc;
	      imgElement.classList.remove("hide");
	    }, 1000);
	  };

	  // ---------- ROTATION ----------
	  const startRotation = (imgElement, imageArray, interval) => {
	    if (!imgElement || imageArray.length === 0) return;

	    let index = 0;
	    imgElement.src = getSrc(imageArray[index]);

	    setInterval(() => {
	      index = (index + 1) % imageArray.length;
	      updateImage(imgElement, getSrc(imageArray[index]));
	    }, interval);
	  };

	  // ---------- DESKTOP ----------
	  startRotation(desktopImg1, array1, 5000);
	  startRotation(desktopImg2, array2, 15000);

	  // ---------- MOBILE (EXACT SAME LOGIC) ----------
	  startRotation(mobileImg1, array1, 5000);
	  startRotation(mobileImg2, array2, 15000);
	};

